export default {
	data() {
		return {
			index: '',
			title:'',
			content:''
		}
	},
	methods: {
		
	},
	onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
	    console.log(option); //打印出上个页面传递的参数。
		this.index = option.id;
		uni.getStorage({
			key:this.index + 'PM_Content',
			success: (res) => {
				this.content = res.data
			}
		})
		uni.getStorage({
			key:this.index + 'PM_Title',
			success: (res1) => {
				this.title = res1.data
			}
		})
	}
	
}
